import * as React from "react"
import { graphql, PageProps } from "gatsby"
import Layout from "@lekoarts/gatsby-theme-minimal-blog/src/components/layout"
import Seo from "@lekoarts/gatsby-theme-minimal-blog/src/components/seo"

interface AboutQuery {
  page: {
    title: string
    slug: string
    excerpt: string
  }
}

const About = ({ data, children }: PageProps<AboutQuery>) => {
  const page = data.page

  // Add target="_blank" to external links in header
  React.useEffect(() => {
    const addTargetBlank = () => {
      const headerLinks = document.querySelectorAll('header a[href^="http"]');
      headerLinks.forEach((link) => {
        if (!link.getAttribute('target')) {
          link.setAttribute('target', '_blank');
          link.setAttribute('rel', 'noopener noreferrer');
        }
      });
    };

    // Run immediately
    addTargetBlank();
    
    // Also run after a short delay to ensure DOM is ready
    setTimeout(addTargetBlank, 100);
  }, []);

  return (
    <Layout>
      <Seo title={page?.title} />
      {children}
    </Layout>
  )
}

export default About

export const query = graphql`
  query AboutQuery {
    page(slug: { eq: "/about" }) {
      title
      slug
      excerpt
    }
  }
`
